.app-header {
  /* position: fixed; */
  /* z-index: 10; */

  width: 100%;
  height: 70px;

  background-color: #54626e;

  color: #ffffff;
}
.header-wrapper {
  padding: 0 80px;

  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.header-block {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header-link {
  color: #ffffff;
  font-size: 32px;
  margin-right: 50px;
  text-transform: uppercase;

  /* border-bottom: 3px solid #ffffff; */

  cursor: pointer;

  transition: 0.3s;
  opacity: 0.8;
}

.header-link:last-child {
  margin-right: 0px;
}

.header-link:hover {
  opacity: 1;
  transform: scale(1.02);
}
