.search-block {
  width: 500px;
  height: 100px;
  background-color: #4c5a65;
  margin: 10px auto;
  border-radius: 10px;

  display: flex;
  align-items: center;
  justify-content: center;
}
.search-input {
  width: 254px;
  height: 36px;
  padding: 0px 14px;
  margin-right: 20px;

  border: 1px solid #00d9ff;
  border-radius: 30px;

  color: #4c5a65;
  font-size: 20px;

  /* background: radial-gradient(
    118.88% 606.86% at 0% 0%,
    rgba(36, 197, 219, 0.15) 0%,
    rgba(36, 197, 219, 0) 100%
  ); */
}
.search-input:focus {
  outline: 0;
}
