.input-table {
  display: flex;
  flex-direction: column;
  margin: 10px auto;

  width: 600px;
  background-color: #cecece;
  border-radius: 10px;
  padding: 10px;
}

.input-row{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5px;
}

.input-name{
  width: 150px;
}

.input-input{
  width: 260px;
}

.input-error{
  width: 180px;
  font-weight: bold;
  color: #ff0000;
}

.input-button{
  width: 50%;
  margin: 10px auto;
}