.formitem-block {
  width: 260px;
  /* height: 170px; */
  padding: 20px;

  margin-bottom: 20px;
  margin-left: 20px;

  border: 1px solid #ffffff;
  border-radius: 30px;

  color: #fff;
  font-size: 16px;

  background-color: #4c5a65;

  transition: 0.5s;

  /* cursor: pointer; */
}

.formitem-name {
  font-size: 1.2rem;
  margin-bottom: 15px;
  text-align: center;
}

.formitem-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.formitem-info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.formitem-picture {
  max-width: 100%;
  height: 100px;
  /* background-color: aqua; */
}