.item-block {
  width: 250px;
  height: 170px;
  padding: 20px;

  margin-bottom: 20px;
  margin-left: 20px;

  border: 1px solid #ffffff;
  border-radius: 30px;

  color: #fff;
  font-size: 16px;

  background-color: #4c5a65;

  transition: 0.5s;

  cursor: pointer;
}

.favourite {
  /* border: 1px solid #ffeb3b; */
  background-color: #2373b2;
}

.item-name {
  font-size: 1.2rem;
  margin-bottom: 15px;
  text-align: center;
}

.item-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.item-info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.item-picture {
  width: 90px;
  height: 90px;
  /* background-color: aqua; */
}