.api-search-block {
  width: 500px;
  height: 100px;
  background-color: #00d9ff;
  margin: 10px auto;
  border-radius: 10px;

  display: flex;
  align-items: center;
  justify-content: center;
}
.api-search-input {
  width: 350px;
  height: 36px;
  padding: 0px 14px;
  margin-right: 20px;

  border: 1px solid #4c5a65;
  border-radius: 30px;

  color: #4c5a65;
  font-size: 20px;
}
.api-search-input:focus {
  outline: 0;
}
