.modal {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
}
.modal-overlay {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #e7e7e7cf;
}
.modal-item-block {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  width: 260px;
  padding: 20px;
  border: 1px solid #ffffff;
  border-radius: 30px;
  color: #fff;
  font-size: 16px;
  background-color: #4c5a65;
  transition: 0.5s;
}
.modal-item-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px
}
.modal-item-name {
  font-size: 1.2rem;
}
.modal-item-close {
  font-size: 2rem;
  transition: 0.5s;
  cursor: pointer;
}
.modal-item-close:hover {
  color: #ff0000
}
.modal-item-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.modal-item-info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.modal-item-picture {
  max-width: 100%;
  height: 260px;
  margin-bottom: 15px;
}
